@import url(https://fonts.googleapis.com/css?family=Lato|Inconsolata&display=swap);
.timer{color:#e5e5e5;margin-bottom:2rem;margin-top:8rem}.timer__display{font-family:"Inconsolata", monospace;font-size:4.5rem}.timer__type{font-size:1.5rem}@media (max-width: 450px){.timer__display{font-size:5.5rem}.timer__type{font-size:2rem}}

.timer-controller{height:100%;display:grid;grid-template-rows:auto 1fr}.control-panel{display:flex;flex-direction:column;justify-content:space-between;font-family:inherit;color:#8d8d8d}.control-panel__start{display:flex;justify-content:center;align-items:center;background-color:#2e374b;border:2px solid #8d8d8d;border-radius:50%;width:4rem;height:4rem;margin:0.5rem auto;font-size:1.5rem;color:inherit;-webkit-transition:background-color 0.2s ease-in-out;transition:background-color 0.2s ease-in-out;cursor:pointer}.control-panel__start:hover{background-color:#414e6b}.control-panel__menu{display:flex;justify-content:space-between;align-items:center;padding:1rem}.control-panel__menu-left{display:grid;grid-template-rows:auto auto;grid-gap:1rem;gap:1rem}.control-panel__menu-left h3{color:#e5e5e5}.control-panel__skip{font-size:1.5rem;-webkit-transition:color 0.2s ease-in-out;transition:color 0.2s ease-in-out;cursor:pointer}.control-panel__skip:hover{color:#fe4e4d}.control-panel__reset{cursor:pointer;-webkit-transition:color 0.2s ease-in-out;transition:color 0.2s ease-in-out}.control-panel__reset:hover{color:#fe4e4d}@media (max-width: 450px){.timer-controller{grid-template-rows:2fr 1fr;align-items:center}.control-panel__start:hover{background-color:#2e374b}.control-panel__skip:hover,.control-panel__reset:hover{color:#8d8d8d}}

.hamburger-button{display:flex;flex-direction:column;justify-content:space-evenly;height:30px;width:30px;background-color:transparent;border:none;cursor:pointer;outline:none}.hamburger-button__line{width:30px;height:1px;background-color:#e5e5e5}

.menu{height:100%}.menu__header{font-size:1.2rem}.menu__form{display:grid;height:90%;margin-top:1rem;grid-template-rows:repeat(3, 1fr)}.form-control__value{background-color:#181c25;color:#e5e5e5;font-family:"Inconsolata", sans-serif;width:45px;margin:1rem auto;border-radius:3px}.form-control__input{width:90%;-webkit-appearance:none;background:transparent}.form-control__input::-webkit-slider-thumb{-webkit-appearance:none;height:1rem;width:1rem;background-color:#fe4e4d;border-radius:50%;position:relative;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);cursor:pointer}.form-control__input::-moz-range-thumb{border:none;height:1rem;width:1rem;background-color:#fe4e4d;border-radius:50%;cursor:pointer}.form-control__input::-webkit-slider-runnable-track{background-color:#181c25;height:3px;cursor:pointer}.form-control__input::-moz-range-track{background-color:#181c25;height:3px;cursor:pointer;outline:transparent}.form-control__input:focus{outline:none}@media (max-width: 450px){.menu__header{font-size:1.5rem}.form-control{font-size:1.2rem}.form-control__value{width:55px}}

.toolbar{position:absolute;width:346px;border-radius:5px}.toolbar__nav{background-color:#2e374b;padding:0 0.5rem;display:grid;grid-template-columns:1fr 8fr 1fr;align-items:center;height:48px;text-align:center}.toolbar__title{color:#1df737}.toolbar__menu{background-color:#3c4457;height:400px;display:none;opacity:0}.toolbar__menu--open{display:block;opacity:1}.toolbar__footer{height:48px;background-color:#2e374b}@media (max-width: 450px){.toolbar{width:100vw;height:100vh}.toolbar__title{font-size:1.5rem}.toolbar__nav{height:10%}.toolbar__menu{height:85%}.toolbar__footer{height:5%}}

*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Lato", sans-serif;background-color:#171b25}a{text-decoration:none}.app{background-color:#2e374b;border:2px solid #444444;border-radius:5px;width:350px;height:500px;margin:1rem auto;text-align:center}@media (max-width: 450px){.app{width:100vw;height:100vh;margin:0}}

