.menu {
  height: 100%;

  &__header {
    font-size: 1.2rem;
  }

  &__form {
    display: grid;
    height: 90%;
    margin-top: 1rem;
    grid-template-rows: repeat(3, 1fr);
  }
}

.form-control {
  &__value {
    background-color: #181c25;
    color: #e5e5e5;
    font-family: "Inconsolata", sans-serif;
    width: 45px;
    margin: 1rem auto;
    border-radius: 3px;
  }

  &__input {
    width: 90%;
    -webkit-appearance: none;
    background: transparent;

    &::-webkit-slider-thumb {
      -webkit-appearance: none;
      height: 1rem;
      width: 1rem;
      background-color: #fe4e4d;
      border-radius: 50%;
      position: relative;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
    }

    &::-moz-range-thumb {
      border: none;
      height: 1rem;
      width: 1rem;
      background-color: #fe4e4d;
      border-radius: 50%;
      cursor: pointer;
    }

    &::-webkit-slider-runnable-track {
      background-color: #181c25;
      height: 3px;
      cursor: pointer;
    }

    &::-moz-range-track {
      background-color: #181c25;
      height: 3px;
      cursor: pointer;
      outline: transparent;
    }

    &:focus {
      outline: none;
    }
  }
}

@media (max-width: 450px) {
  .menu {
    &__header {
      font-size: 1.5rem;
    }
  }

  .form-control {
    font-size: 1.2rem;

    &__value {
      width: 55px;
    }
  }
}
