.timer {
  color: #e5e5e5;
  margin-bottom: 2rem;
  margin-top: 8rem;

  &__display {
    font-family: "Inconsolata", monospace;
    font-size: 4.5rem;
  }

  &__type {
    font-size: 1.5rem;
  }
}

@media (max-width: 450px) {
  .timer {
    &__display {
      font-size: 5.5rem;
    }

    &__type {
      font-size: 2rem;
    }
  }
}
